import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { spreadProps } from '@open-wc/lit-helpers'; 



const meta:Meta={
    title:"charts/BubbleChart",
    component:'bubble-chart',
    tags: ['autodocs'],
    argTypes:{
        myHeight:{
            control:"number",
            description:"Height of the chart"
        },
        myWidth:{
            control:"number",
            description:"Width Percentage of the chart"
        },
        myTitle:{
            control:"text",
            description:"Title of the chart"
        },
        xTitle:{
            control:"text",
            description:"Title of the X axis"
        },
        yTitle:{
            control:"text",
            description:"Title of the Y axis"
        },
        chartData: {
            control: 'object', // Allows editing the data array in Storybook controls
            description: 'Optional: Custom chart data (array of CountryBubbleData objects)',
            defaultValue: undefined, // Indicates it's optional, will use component's default if not provided
        },
    },
    render: (args) =>html`<bubble-chart ${spreadProps(args)}></bubble-chart>`
}
export default meta;

export const BubbleChartFirst: StoryObj ={
    args:{
        myHeight: 350,
        myWidth: 100,
        myTitle: 'Population by Country',
        xTitle: 'Country',
        yTitle: 'Population',
        // bubbleData: [
        //     { country: 'USA', population: 331002651, world_land_area_percentage: 6.1 },
        //     { country: 'China', population: 1439323776, world_land_area_percentage: 9.6 },
        //     { country: 'India', population: 1380004385, world_land_area_percentage: 2.4 },
        //     { country: 'Brazil', population: 212559417, world_land_area_percentage: 5.7 },
        //     { country: 'Russia', population: 145912025, world_land_area_percentage: 11.5 }
        // ]
    }
}